body {
    font-family: Arial;
    margin: 0;
}

.background {
    margin-left: auto;
    margin-right: auto;
}

.slider {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-size: cover;
    background-position: center;
    min-height: 80dvh;
}

.slider.home {
    background-image: url("../public/room.png");
}

@media(max-width: 1000px) {
    .slider.home {
        background-image: url("../public/roomCropped.png");
    }
}

.slider-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
}

.slider-h1 {
    font-size: clamp(1.5rem, 8vw, 4rem);
    color: #08519c;
}

.slider-h1.left {
    width: 50%;
    min-width: min(calc(100% - 2rem), 400px);
    padding: 1rem;
}

.slider-h2 {
    font-size: clamp(1.6rem, 8vw, 4rem);
    color: #faf9f6;
    font-family: Great Vibes;
}

.slider-h2.left {
    width: 50%;
    min-width: min(calc(100% - 2rem), 400px);
    padding: 1rem;
}

.learn-more-button {
    text-align: center;
    color: white;
    background-color: #08519c;
    border: none;
    border-radius: 0.4em;
    padding: 0.75em;
    font-weight: bold;
    font-size: clamp(0.8rem, 2.6vw, 1.3rem);
    margin: 0.75em;
    transition: background-color 0.25s ease-in-out;
}

.learn-more-button:hover {
    background-color: #3182bd;
}

.learn-more-button.purple {
    background-color: #c6dbef;
    color: #0808519c
}

.learn-more-button.purple:hover {
    background-color: #6baed6;
}

.contact-button {
    text-align: center;
    color: white;
    background-color: #9e9acb;
    border: none;
    border-radius: 1rem;
    padding: 1rem;
    font-size: 2rem;
    margin-left: auto;
    margin-right: auto;
    width: 20rem;
    transition: background-color 0.25s ease-in-out;
}

.contact-button:hover {
    background-color: #756bb1;
}

.tag-img {
    height: 3rem;
    width: auto;
}
